<template>
  <div id="capture-quize">
    <head-top title="截屏提问"></head-top>
    <div class="main">
      <div class="top">
        <div class="tt">
          <span class="info fL">
            {{questionInfo['SSSerial']}}.[{{getQuestionTypeName(questionInfo['SSType'])}}]
            {{questionInfo['SSScore']}}分
          </span>
          <span :class="questionPattern=== 3 ? 'red':'green'">
            {{getPatternName(questionPattern)}}
          </span>
          <span class="time fR">
            计时 ：{{questionInfo['SSTime']}}分钟
          </span>
        </div>
        <div class="bg">
          <r-image width="92vw" height="33vh" :src="img"></r-image>
        </div>
      </div>

      <div class="responder" v-if="showResponder">
        <div v-if="ableResponder">
          <div class="prompt">请点击下方按钮进行抢答</div>
          <div class="qiangdabg" @click="sbumit">
            <van-loading v-if="responderLoading" type="spinner" color="#1989fa">
              抢答中
            </van-loading>
          </div>
        </div>
        <div v-else :class="responderResult?'qd_sucess':'qd_fail'">
          {{responderResult?'':'抢答失败'}}
        </div>
      </div>
      <div v-else>
        <do-answer v-if="showDoAnswer" :question-type="questionInfo['SSType']"
                   :question-option="questionInfo['SSOption']"
                   :ableReply="ableReply" :answer-value="questionOperate['SAContent']" :module-picture="modulePicture"
                   @change="answerChange">
          <!-- 提交 -->
          <div v-show="questionInfo['State']===1 && ableReply">
            <button class="sbumit" @click="submitAnswer">
              提 交
            </button>
          </div>
        </do-answer>
        <my-answer v-if="showMyAnswer" :question-type="questionInfo['SSType']" :my-answer="questionOperate['SAContent']"
                   :module-picture="modulePicture" :true-answer="questionInfo['SSAnswer']"
                   :get-score="questionOperate ? questionOperate['SAScore'] : null"
                   :comment="questionOperate['Comment']||''">
        </my-answer>
        <survey ref="survey" v-if="showSurvey" :disable="questionInfo['State'] !== 3"
                :master="questionOperate['SAMaster'] "
                @selectSurvey="selectSurvey">
        </survey>
      </div>
    </div>
  </div>
</template>

<script>
import RImage from "@/components/r-image";
import DoAnswer from "@/components/Question/DoAnswer";
import MyAnswer from "@/components/Question/MyAnswer";
import Survey from "@/components/Question/Survey";

import {
  ModulePicture,
  ScreenPattern,
  screenPatternName,
  screenQuestionTypeName,
  ScreenFunc
} from "@/utils/enum";
import { setTimeout } from "timers";

export default {
  name: "",
  components: { RImage, DoAnswer, MyAnswer, Survey },
  props: {},
  data () {
    return {
      questionID: this.$route.params.id,
      operateStep: ScreenFunc.init, //操作步骤 1获取提问和答题 2抢答开始 3抢答成功开始答题 4下发答案和解析 5调研
      questionInfo: {},
      questionPattern: ScreenPattern.all, // 截屏提问模式 1全员作答,2随机挑人,3抢答模式
      questionOperate: {},
      ableResponder: false, // 是否可以抢答
      responderResult: false, // 抢答成功
      responderLoading: false,
      img: "",
      modulePicture: ModulePicture.ScreenAnswer,
      ableReply: false,
      isSurvey: false
    };
  },
  computed: {
    // 显示抢答操作
    showResponder () {
      return this.questionPattern === ScreenPattern.responder && this.operateStep === ScreenFunc.responder && this.questionInfo["State"] === 1
    },
    // 什么时候显示 作答
    showDoAnswer () {
      if (!this.ableReply) {
        return false; //已经作答的不再显示作答
      } else if (this.questionPattern === ScreenPattern.random) {
        return (
          this.operateStep === ScreenFunc.init &&
          this.questionInfo["State"] === 1 &&
          this.questionOperate["StudentID"] === this.$store.getters.studentID
        );
      } else if (this.questionPattern === ScreenPattern.responder) {
        if (this.operateStep === ScreenFunc.responderReply && this.questionInfo["State"] === 1 && this.responderResult) {
          return true;
        } else if (this.operateStep === ScreenFunc.init && this.questionInfo["State"] === 1 && this.questionOperate["StudentID"] === this.$store.getters.studentID) {
          return true;
        } else {
          return false;
        }
      } else {
        return this.operateStep === ScreenFunc.init && this.questionInfo["State"] === 1;
      }
    },
    // 什么时候显示 我的答案
    showMyAnswer () {
      if (!this.ableReply) {
        return true; //已经作答的 直接显示我的答案
      } else if (
        this.operateStep === ScreenFunc.acceptAnswer ||
        this.operateStep === ScreenFunc.survey
      ) {
        return true;
      } else if (
        this.operateStep === ScreenFunc.init &&
        [2, 3].indexOf(this.questionInfo["State"]) > -1
      ) {
        return true;
      } else {
        return false;
      }
    },
    // 什么时候显示 我的调研
    showSurvey () {
      return (
        this.isSurvey &&
        this.questionOperate &&
        this.questionOperate["SAContent"]
      );
      // // 非全员作答模式 都没有调研
      // if (this.questionPattern !== ScreenPattern.all) {
      //   return false;
      // } else {
      //   // 没有作答就没有权限 提交调研
      //   if (!this.questionOperate || !this.questionOperate["SAContent"]) {
      //     return false;
      //   } else if (this.operateStep === ScreenFunc.survey) {
      //     return true;
      //   } else if (this.operateStep === ScreenFunc.init) {
      //     return this.questionInfo["State"] !== 1;
      //   }
      //   return false;
      // }

      // if (this.operateStep === ScreenFunc.survey) {
      //   return true;
      // } else if (
      //   this.operateStep === ScreenFunc.init &&
      //   this.questionInfo["State"] !== 1
      // ) {
      //   return true;
      // } else {
      //   return false;
      // }
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.getQuestionData();
    });
  },
  methods: {
    getQuestionData () {
      Object.keys(this.questionInfo).forEach(item => {
        this.$delete(this.questionInfo, item);
      });
      Object.keys(this.questionOperate).forEach(item => {
        this.$delete(this.questionOperate, item);
      });
      this.$mainHttp.screenQuestion.getData({
        ssid: this.questionID,
        studentID: this.$store.getters.studentID
      }).then(res => {
        if (res.Result) {
          this.questionInfo = JSON.parse(JSON.stringify(res.Keys[0]));
          this.isSurvey = this.questionInfo["SSSurvey"] || false;
          this.questionOperate = JSON.parse(JSON.stringify(res.Values[0]));
          this.ableReply = !(this.questionOperate && this.questionOperate["SAContent"]);
          this.questionPattern = this.questionInfo["SSPattern"] || ScreenPattern.all;

          this.img = this.$mainHttp.common.picture(
            this.questionInfo["SSContent"],
            ModulePicture.ScreenSubject
          );

          // // 未抢答允许用户可以再次抢答
          // if (
          //   this.questionPattern === ScreenPattern.responder &&
          //   this.operateStep === ScreenFunc.init &&
          //   this.questionInfo["State"] === 1 &&
          //   !this.questionOperate["StudentID"]
          // ) {
          //   this.ableResponder = true;
          // }
        }
      }).catch(() => {
      });
    },
    getPatternName (val) {
      return screenPatternName(val);
    },
    getQuestionTypeName (val) {
      return screenQuestionTypeName(val);
    },
    sbumit () {
      if (this.responderLoading || !this.ableResponder) return;
      this.responderLoading = true;
      this.$mainHttp.screenQuestion.postResponder({
        ciID: this.$store.getters.courseID,
        ssID: this.questionInfo["SSID"],
        studentID: this.$store.getters.studentID
      }).then(res => {
        if (res.Result) {
          this.responderResult = true;
          // 抢答成功，可开始作答
          setTimeout(() => {
            this.getQuestionData();
            this.operateStep = ScreenFunc.responderReply;
          }, 1000);
        } else {
          this.responderResult = false;
        }
        this.responderLoading = false;
        this.ableResponder = false;
      }).catch(() => {
        this.responderLoading = false;
        this.ableResponder = true;
      });
    },
    answerChange (answer) {
      this.questionOperate["SAContent"] = answer;
    },
    submitAnswer () {
      if (!this.questionOperate["SAContent"]) {
        this.$toast("请先作答");
      } else {
        this.$mainHttp.screenQuestion.postAnswer({
          ciID: this.$store.getters.courseID,
          saID: this.questionOperate["SAID"],
          ssID: this.questionInfo["SSID"],
          studentID: this.$store.getters.studentID,
          ssPattern: this.questionPattern,
          saContent: this.questionOperate["SAContent"]
        }).then(res => {
          if (res.Result) {
            this.$toast.success("作答成功");
            setTimeout(() => {
              this.ableReply = false;
            }, 200);
          } else if (res.IsAnswer) {
            this.$toast.success("您已作答, 请勿再答");
          } else if (res.Info) {
            this.$toast(res.Info);
          }
        }).catch(() => {
          this.$toast("提交失败，请重试");
        });
      }
    },
    selectSurvey (val) {
      this.$mainHttp.screenQuestion.postSurvey({
        ciID: this.$store.getters.courseID,
        ssID: this.questionInfo["SSID"],
        studentID: this.$store.getters.studentID,
        saMaster: val ? 1 : 0
      }).then(res => {
        if (res.Result) {
          this.$toast.success("提交问卷成功");
          setTimeout(() => {
            this.getQuestionData();
          }, 1000);
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("提交失败");
        }
      }).catch(() => {
        this.$toast("提交失败，请重试");
      });
    }
  },
  watch: {
    "$store.getters.screenQuestion" (newVal, oldVal) {
      // console.log("newVal:", newVal);
      if (newVal !== oldVal) {
        this.operateStep = newVal.type || ScreenFunc.init;
        this.ableResponder = this.operateStep === ScreenFunc.responder;
        let isChangeId = this.questionID !== newVal.id;
        this.questionID = newVal.id || ""; // 这个必须放在 下面判断之前
        // 是否刷新题目(问卷调研刷新，是避免因为问答题没下发导致未刷新而异常)
        if (
          this.operateStep === ScreenFunc.init ||
          this.operateStep === ScreenFunc.acceptAnswer ||
          this.operateStep === ScreenFunc.survey
        ) {
          this.getQuestionData();
        } else if (isChangeId) {
          this.getQuestionData();
        }

        if (
          this.operateStep === ScreenFunc.survey ||
          this.operateStep === ScreenFunc.acceptAnswer
        ) {
          this.isSurvey = this.operateStep === ScreenFunc.survey;
          this.$nextTick(() => {
            let height = document.body.clientHeight;
            document.documentElement.scrollTop = height;
            setTimeout(() => {
              document.documentElement.scrollTop = height;
            }, 1000);
          });
        }
      }
    }
  }
};
</script>

<style lang='less' scoped>
@import url("./../../../styles/common/base.less");

#capture-quize {
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 1);

  .main {
    .top {
      width: 100%;

      .tt {
        height: 30px;
        line-height: 30px;
        margin: 0 10px;
        font-family: MicrosoftYaHei;
        display: flex;
        justify-content: space-between;

        span {
          &:nth-child(1) {
            font-size: @font-sub-title;
            font-weight: 400;
          }

          &:nth-child(2),
          &:nth-child(3) {
            font-size: @font-info;
          }
        }

        .red {
          color: #e97b7b;
        }

        .green {
          color: #00cbab;
        }
      }

      .bg {
        max-width: 92vw;
        max-height: 33vh;
        background-size: cover;
        padding: 10px 0;
        margin: 0 auto;
      }
    }

    .responder {
      margin-top: 15px;
      width: 100%;
      padding-bottom: 3vh;
      text-align: left;
      background: rgba(255, 255, 255, 1);
      overflow: hidden;
      position: relative;

      .prompt {
        left: 16px;
        top: 30px;
        color: rgba(51, 51, 51, 1);
        font-size: @font-info;
        position: absolute;
      }

      .qiangdabg {
        background-image: url("../../../assets/qiangdaanniu.png");
        width: 150px;
        height: 150px;
        background-size: cover;
        opacity: 1;
        margin: 0 auto;
        margin-top: 20%;
        line-height: 150px;
        text-align: center;
        color: #f44;
        font-size: 20px;
      }

      .qd_sucess {
        background-image: url("../../../assets/qdcg.png");
        width: 150px;
        height: 150px;
        background-size: cover;
        opacity: 1;
        margin: 0 auto;
        margin-top: 20%;
      }

      .qd_fail {
        background: none;
        text-align: center;
        font-size: @font-title * 1.3;
        color: #f44;
      }
    }

    .datiRrea {
      width: 92%;
      text-align: left;
      background: rgba(255, 255, 255, 1);

      .prompt {
        margin-bottom: 4vw;
      }

      padding: 4vw;

      .area {
        color: #999999;
        position: relative;
        width: 92vw;
        height: 25vh;

        textarea {
          width: 100%;
          height: 100%;
          line-height: 13px;
          background: rgba(235, 240, 239, 1);
          border-radius: 3px;
          box-sizing: border-box;
          border: none;
          padding: 10px;
        }

        i {
          position: absolute;
          left: 10px;
          bottom: 5px;
        }

        span {
          position: absolute;
          right: 10px;
          bottom: 5px;
        }
      }
    }

    .sbumit {
      width: 60vw;
      height: 45px;
      background: rgba(0, 203, 171, 1);
      border-radius: 5px;
      margin: 25px 0;
      font-size: @font-sub-title;
    }
  }
}
</style>



